export default class APIService {
  static UpdateClient(client_id, body, token) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/api/client/${client_id}/`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token}`,
      },
      body: JSON.stringify(body),
    }).then((response) => response.json());
  }

  static UpdateProject(project_id, body, token) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/api/project/${project_id}/`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token}`,
      },
      body: JSON.stringify(body),
    }).then((response) => response.json());
  }

  static CreateProject(body, token) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/api/project/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token}`,
      },
      body: JSON.stringify(body),
    }).then((response) => response.json());
  }

  static CreateClient(body, token) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/api/client/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token}`,
      },
      body: JSON.stringify(body),
    }).then((response) => response.json());
  }

  static DeleteClient(client_id, token) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/api/client/${client_id}/`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token}`,
      },
    });
  }

  static DeleteProject(project_id, token) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/api/project/${project_id}/`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token}`,
      },
    });
  }
  static LoginUser(body) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/auth/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    }).then((response) => response.json());
  }

  static CreateUser(body) {
    return fetch(`https://projectmanagementappv2.azurewebsites.net/api/users/`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body),
    }).then((response) => response.json());
  }
}
