import React, { useState, useEffect } from "react";
import { useCookies } from "react-cookie";
import "./Project.css";
import APIService from "../../APIService";
import FormProject from "./FormProject";
import DetailProject from "./DetailProject";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { formatDate, addDays, diffInDays, isLeapYear } from '@rakeshmalik593/date-time-utils';
import dateFormat from "dateformat";

const today = new Date();

const Project = () => {
  const [projects, setProjects] = useState([]);
  const [token] = useCookies(["loginToken"]);
  // const dateTime = addDays(new Date(), 5);
  // const now  = new Date();
  useEffect(() => {
    fetch("https://projectmanagementappv2.azurewebsites.net/api/project", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token["loginToken"]}`,
      },
    })
      .then((response) => response.json())
      .then((response) => setProjects(response)) // update projects state variable

      .catch((error) => console.log(error));
  }, [token]);

  const [ProjectInstance, setProjectInstance] = useState(null);

  const editProjectBtn = (project) => {
    setProjectInstance(project);
  };

  const deleteProjectBtn = (project) => {
    APIService.DeleteProject(project.id, token["loginToken"])
      .then(() => {
        const new_project = projects.filter((myproject) => {
          if (myproject.id === project.id) {
            return false;
          } else {
            return true;
          }
        });
        setProjects(new_project);
        toast.success('Project deleted successfully!');
      })
      .catch((error) => console.log(error));
  };
  const updatedProjects = (project) => {
    const new_project = projects.map((myproject) => {
      if (myproject.id === project.id) {
        return project;
      } else {
        return myproject;
      }
    });
    setProjects(new_project);
  };

  const projectNewForm = () => {
    setProjectInstance({
      name: "",
      deadline: "",
      description: "",
      status: "",
      client: "",
    });
  };

  const createdProject = (project) => {
    const new_project = [...projects, project];
    setProjects(new_project);
  };

  const [ActiveProject, setActiveProject] = useState();
  const [ProjectTodos, setProjectTodos] = useState();

  const viewProjectDetail = (project) => {
    setActiveProject(project);
    fetch(`https://projectmanagementappv2.azurewebsites.net/api/todos?project=${project.id}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Token ${token["loginToken"]}`,
      },
    })
      .then((response) => response.json())
      .then((response) => setProjectTodos(response));
  };

  return (
    <div style={{ marginTop: "5em" }}>
      <div>
      {/* Current date and time: {formatDate(now)} */}
    </div>
      <div className="d-flex align-items-center justify-content-between">
        <div className="project-header">
          <h1 style={{ color: "burlywood" }}>Your Exising Projects</h1>
        </div>
        <div className="project-header-btn d-flex">
          <div className="input-group">
            <select className="custom-select" id="inputGroupSelect01">
              <option selected>Filter...</option>
              <option value="1">One</option>
              <option value="2">Two</option>
              <option value="3">Three</option>
            </select>
          </div>
          <button
            className="btn btn-primary btn-sm project-add-btn"
            onClick={projectNewForm}
          >
            &#43; Add a new project
          </button>
        </div>
      </div>

      <hr className="bg-light" />
      <div className="proj-wrapper text-white">
        {projects.map((project) => {
          console.log(project)
          return (
            <div key={project.id} className="">
              <h3>{project.name}</h3>
              <p>Deadline: {dateFormat(project.deadline, "mmmm dS, yyyy")}</p>
              {/* <p>Number of days left: {diffInDays(new Date(project.deadline),today)+1}</p> */}
              <p>Technology Stack: {project.description}</p>
              <p>Status: {project.status}</p>
              <div className="project-btn-wrapper">
                <button
                  onClick={() => viewProjectDetail(project)}
                  className="btn btn-primary btn-sm"
                >
                  View
                </button>
                <button
                  onClick={() => editProjectBtn(project)}
                  className="btn btn-primary btn-sm mx-3"
                >
                  Update
                </button>
                <button
                    onClick={() => deleteProjectBtn(project)}
                    className="btn btn-delete btn-sm"
                  >
                    Delete
                  </button>
              </div>
              <hr />
            </div>
          );
        })}
      </div>
      {ProjectInstance ? (
        <FormProject
          ProjectInstance={ProjectInstance}
          setProjectInstance={setProjectInstance}
          updatedProjects={updatedProjects}
          createdProject={createdProject}
        />
      ) : null}

      {ActiveProject ? (
        <DetailProject
          ActiveProject={ActiveProject}
          setActiveProject={setActiveProject}
          ProjectTodos={ProjectTodos}
        />
      ) : null}
    </div>
  );
};

export default Project;
